<template>
	<view>
		<form>
			<view class="cu-form-group">
				<view class="title">手机号</view>
				<input placeholder="请输入手机号码" v-model="phonenumber"></input>
			</view>
			<view class="grid col-1 padding-sm bg-white margin-top" @tap="update">
				<view class="margin-tb-sm text-center">
					<button class="cu-btn bg-blue lg">保存</button>
				</view>
			</view>
		</form>


	</view>
</template>

<script>
export default {
	data() {
		return {
			phonenumber: ''
		}
	},
	onLoad(options) {
		var o = options.phonenumber
		if (o == null || o == 'null') {
			o = ''
		}
		this.phonenumber = o
	},
	methods: {
		// 修改
		update() {
			this.vusui.load(3)
			this.http.post('', {
				phonenumber: this.phonenumber
			}).then((res) => {
				this.vusui.close("loading");
				if (res.code != 0) {
					this.vusui.alert(res.msg)
					return;
				} else {
					this.vusui.alert('修改成功', {
						yes: function () {
							var pages = getCurrentPages();
							//上一个页面
							var prevPage = pages[pages.length - 2];
							var data = {
								needReflush: 1
							}
							prevPage.$vm.data = data
							uni.navigateBack();
						}
					})
				}
			})
		}
	}
}
</script>